import React,{useEffect,useState} from 'react'
import { useParams , useHistory } from 'react-router'
import {updateads,getMyAds} from '../services/myAdsService'
import { Container, Card, Row, Col } from 'react-bootstrap'
// import { getCategories } from '../services/categoriesService'
import { getRegions } from '../services/regionsService'
// import { getCarMakes } from '../services/carsService'
import Input from '../components/common/Input'
import { t , getLang} from '../utils/helper'
import LoadingButton from '../components/common/LoadingButton'
import { toast } from 'react-toastify'
export default function Editescreen() {
    const i18n = getLang()
    const history = useHistory()
    const {id} = useParams() 
    const [isSubmitting, setIsSubmitting] = useState(false)
    const [state, setstate] = useState([])
  const [regions, setRegions] = useState([])
  // const [makes, setMakes] = useState([])
  // const [categories, setCategories] = useState([])
useEffect(() => {

  const getallads = async () => {
      const data = await  getMyAds()
      const myads = data.filter(el => el._id === id)
      setstate({...myads[0],region :myads[0].region?._id })
  }

 getallads()

  async function fetchCategories() {
    try {
      // const categories = await getCategories()
      // setCategories(categories)
    } catch (ex) {}
  }

 fetchCategories()

  async function fetchRegions() {
    try {
      const regions = await getRegions()
      setRegions(regions)
    } catch (ex) {}
  }

 fetchRegions()

  async function fetchCarMakes() {
    try {
      // const makes = await getCarMakes()
      // setMakes(makes)
    } catch (ex) {}
  }

 fetchCarMakes()
}, [id])

const onSubmit = async () => {
 
      console.log(state)
      try {
        setIsSubmitting(true)
        const { message } = await updateads(state)
        toast.success(message)
        history.replace('/admin/my-ads')
      } catch (ex) {
        const errorString = ex.message || ex.toString()
        toast.error(errorString)
        setIsSubmitting(false)
      }
}
const handelchange = (event) => {
    const name = event.target.name;
    const value= event.target.value;
    setstate({...state,[name]:value})

}
    return (
        <div>
          <Container>
      <h3 className="border-bottom pb-3 mb-3">{t('update_ad')}</h3>
     
      
      <Row>
   

        <Col lg={10}>
          
        <Card className="pt-3 px-3 mb-3 bg-light">
           {/* Title */}
           <Input
              onChange={handelchange}
              value={state.title}
              type="text"
              name="title"
              label={t('ad_title')}
         
            />
            <Row>
              <Col xs={12}>
                 
              <select name="region" onChange={handelchange} className="form-control" value={state.region}>
               
                {
                   
                      regions.map((el,index) => {return (
                        <option  key={index} value={el._id}> {el.title[i18n]}</option>
                    )})

                    }
                    
                
            </select>
              </Col>

           
            
 {state && state.category?.title?.en  ==="lands" && (
   <>
   <Col xs={12}>
          <Input
              onChange={handelchange}
              value={state.space}
              type="text"
              name="space"
              label={t('newads.surfaces')}
             
            />
            </Col>
   </>
 )}

              {state && state.category?.title?.en === 'properties' && (
                <>
                <Col xs={12}>
                
                  </Col>
                  <Col xs={12}>
                  <Input
              onChange={handelchange}
              value={state.numberOfBathroom}
              type="text"
              name="numberOfBathroom"
              label={t('newads.numbath')}
          
            />
                  </Col>
                  <Col xs={12}>
                  <Input
              onChange={handelchange}
              value={state.numberOfRooms}
              type="text"
              name="numberOfRooms"
              label={t('newads.numbad')}
            
            />
                    <Input
              onChange={handelchange}
              value={state.space}
              type="text"
              name="space"
              label={t('newads.surfaces')}
           
            />
             
                  </Col>
                </>
              )}

              {state.km &&  (
                <>
                 

                  <Col xs={12}>
                    <Input
                      type="number"
                      value={state.km}
                      onChange={handelchange}
                      placeholder="5000"
                      label={t('km')}
                  
                    />
                  </Col>
                </>
              )}
            </Row>
  

           

            {/* Price */}
            <Input
              onChange={handelchange}
              value={state.price}
              type="number"
              name="price"
              label={t('price')}
         
            />

            {/* Details */}
            <Input
              as="textarea"
              rows={5}
              onChange={handelchange}
              value={state.details}
              type="text"
              name="details"
              label={t('ad_details')}
             
            />
            <div className="my-3 d-flex flex-row-reverse">
        <LoadingButton isProcessing={isSubmitting} onClick={onSubmit}>
          {t('publish_ad')}
        </LoadingButton>
      </div>
          </Card>
        </Col>
      </Row>

      
    </Container>
        </div>
    )
}
