import axios from 'axios'
import { getHeaders } from './headers'

export const getModel = async () => {
  const { data: res } = await axios.get('api/admin/car-makes', {
    headers: getHeaders(),
  })

  const success = res.success
  const message = res.message
  if (success === false) throw Error(message)

  const model = res.data 
  return   model
}
export const postModel = async (don) => {
    const { data: res } = await axios.post('/api/admin/car-makes',don, {
      headers: getHeaders(),
    })
    const success = res.success
  const message = res.message
  if (success === false) throw Error(message)

  return { success, message }
  }

  export const updatemodel = async (don) => {
const {data} = await axios.put(`/api/admin/car-makes/${don.id}`,don,{headers:getHeaders()})
const success = data.success
const message = data.message
if(success === false) throw Error(message)
return {success,message}
  }
  export const deletemodel = async (id) => {
    const {data} = await axios.delete(`/api/admin/car-makes/${id}`,{headers:getHeaders()})
    const success = data.success
    const message = data.message
    if(success === false) throw Error(message)
    return {success,message}
      }