import React , {useEffect,useState} from 'react'
import ReactPaginate from "react-paginate";
import { t ,getLang} from '../../utils/helper'
import Formadd from './add'
import {Modal} from 'react-bootstrap'
import { toast } from 'react-toastify'
import { Link } from 'react-router-dom';
import {getModel,updatemodel,deletemodel} from '../../services/modelService'
export default function Index() {
    const   i18n  = getLang();
    const [rowsPerPage , setrowsPerPage] = useState([])
    const [offset , setoffset] = useState(0)
    const [selected , setselected] = useState(0)
    let pagelimit = 5;
    // const [pagelimit,setpagelimit] = useState(5)
    const [show, setShow] = useState(false);
    const [dateshow, setdateshow] = useState([])
    let filterval = [];
    // const [filterval , setfilterval] = useState([])
    const [rowsuprission,setrowsuprission]=useState({})
    const [droweradd, setadddrower] = useState(false)
    useEffect(() => {
        const getmodeldata =async  () => {
          const  datamodel = await getModel()

          if(datamodel.length  > 0){
              console.log(datamodel)
              setrowsPerPage(datamodel)
              const dat =  datamodel.slice(offset, pagelimit+offset)
              if(Boolean(dat.length))
             setdateshow(dat)
             else {
               const dat =  datamodel.slice(0, pagelimit) 
               setselected(0)
               setdateshow(dat)
             }
            }
              else 
              setdateshow([])
        }

        getmodeldata()
      }, [offset , pagelimit])
      const getmodeldata =async  () => {
        const  datamodel = await getModel()

        if(datamodel.length  > 0){
            console.log(datamodel)
            setrowsPerPage(datamodel)
            const dat =  datamodel.slice(offset, pagelimit+offset)
            if(Boolean(dat.length))
           setdateshow(dat)
           else {
             const dat =  datamodel.slice(0, pagelimit) 
             setselected(0)
             setdateshow(dat)
           }
          }
            else 
            setdateshow([])
      }

const  onPageChanged = (data) => {
    let selected = data.selected;
  let offset = Math.ceil(selected * pagelimit);
  if(Boolean(filterval.length))
  { const currentdata = filterval.slice(offset, offset +pagelimit)
   setoffset(offset)
   setselected(selected)
  setdateshow(currentdata)
  }
  else{
    const currentdata = rowsPerPage.slice(offset, offset +pagelimit)
    setoffset(offset)
    setselected(selected)
   setdateshow(currentdata)
   }
  }
  const enabelrow = async (el,id) => {
    try {
    const dataupdate = await updatemodel({isEnabled:el ,id:id})
    if(dataupdate){
        getmodeldata();
      toast.success(dataupdate.message)
  }
  }
  catch (ex) {
    const errorString = ex.message || ex.toString()
    toast.error(errorString)
   
  }
}
  const deleterow = async () => {
    try {
    const deleterow = await deletemodel(rowsuprission._id)
    if(deleterow){
        getmodeldata();
        toast.success(deleterow.message)
        setShow(false)
    }
} catch (ex) {
    const errorString = ex.message || ex.toString()
    toast.error(errorString)
    setShow(false)
  }
}
const hidedrower = () => {
  if(droweradd)
  setadddrower(false)
}
    return (
        <div className="container">
               <h3>{t("carmarkes.titel")}</h3>
              
      <hr/>
      <button onClick={()=>setadddrower(true)} className="addnew btn btn-primary pl"><i className="fal fa-plus pl-2 pr-2"></i>{" "} {t("Admin.btnadd")}</button>
      <div onClick={() => hidedrower()} className={droweradd ?"overlay is-open":"overlay"}></div>
            <div className={(droweradd &&i18n==="ar")    ? "arnav-menu  active" :(droweradd &&i18n==="en") ?"nav-menu  active": "nav-menu"}>
      
       {droweradd && <Formadd  hideSideBar={setadddrower} refrechdata= {getmodeldata} /> }
     
      </div>
            <div dir="ltr" className= {!Boolean(dateshow.length) ? "d-none float-right mt-2" :"float-right mt-2"}>
             <ReactPaginate
          previousLabel={<i className="fal fa-chevron-left"></i>}
          nextLabel={<i className="fal fa-angle-right"></i>}
          breakLabel={'...'}
          breakClassName={'break-me'}
          pageCount={filterval.length >0 ?Math.ceil(filterval.length / pagelimit) : Math.ceil(rowsPerPage.length / pagelimit)}
          marginPagesDisplayed={2}
          pageRangeDisplayed={2}
          onPageChange={onPageChanged}
          containerClassName={'pagination'}
          activeClassName={'active'}
          initialPage={0}
          forcePage={selected}
        /> </div>

<div className="table-responsive">
             <table className="table table-borderless mt-4">
          <thead>
            <tr className=" th-table">
              <th className="col ">{t("carmarkes.id")}</th>
              <th className="col">{t("carmarkes.status")}</th>
              <th className="col">{t("carmarkes.title")}</th>
              <th className="col">{t("carmarkes.action")}</th>
              
            </tr>
          </thead>

          <tbody>
            {" "}
           
            {
              dateshow.map((el, index) => {
            
                return (
                  <tr key={index} className="row custom-tr ">
                    <td label={t("carmarkes.id")} className="col">{el._id.slice(el._id.length - 3,el._id.length)}</td>
                    <td label={t("carmarkes.status")} className="col">{el?.isEnabled? "active" : "not active"}</td>
                    <td label={t("carmarkes.title")} className="col">{el?.title }</td>
                    
                    
                   
                    <td
                      label={t("carmarkes.action")}
                      className="col"

      
        >
         {el.isEnabled ? <i onClick={()=>enabelrow("false",el._id)}  className="fas fa-lock cursor-pointer pl-1 pr-1"></i> : <i class="fas fa-unlock" onClick={()=>enabelrow("true",el._id)}></i> }
          <i  onClick={()=>{setrowsuprission(el) ; setShow(true)}} className="fas fa-trash-alt tabfonticon pr-2 pl-2 cursor-pointer"
                        ></i>
                <Link to={"/admin/Carmodel/"+el._id} ><i className ="fas fa-info cursor-pointer pl-1 pr-1"></i></Link>     
                        </td>
                  </tr>
                );
              })}{" "}
          </tbody>
        </table>
        </div> 

        <Modal show={show} onHide={() => setShow(false)}>
  
  <Modal.Header closeButton>
        <Modal.Title>{t("model.titel")} {" "} {rowsuprission?.title}</Modal.Title>
      </Modal.Header>
    <div className="modal-body">
      {t("model.body")} 
    </div>
    <div className="modal-footer">
      <button type="button" className="btn btn-secondary p-2" onClick={()=> setShow(false)}>{t("modal.cancel")}</button>
      <button type="button" className="btn btn-primary p-2" onClick={() =>deleterow()}>{t("modal.delete")}</button>
    </div>

</Modal>
        </div>
    )
}
