import React,{useState , useEffect} from 'react'
import {moveads} from '../../services/adsService'
import {adminCategory} from '../../services/categoriesService'
import {gettypes} from '../../services/typesServices'
import {getadminregion} from '../../services/regionsService'
 import { t ,getLang} from '../../utils/helper'
 import { toast } from 'react-toastify'

       export default function Changeads({hideSideBar , rowdata , refrechdata}) {
           const i18n = getLang()
    const [region, setregion] = useState([])
    const [category, setcategory] = useState([])
    const [subcategory, setsubcategory] = useState([])
    const [type, settype] = useState([])
    const [state, setstate] = useState({})

    useEffect(() => {
        adminCategory()
        .then(res => {if(res.data.success){
            setcategory(res.data.data)
        }})
        getadminregion()
        .then(res => {if(res.data.success){
            setregion(res.data.data) 
        }})
    }, [])
const handelchange = async (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setstate({...state , [name]:value})
    if(name === 'category'){
        const typesrow = await gettypes(value)
        settype(typesrow)
        const rowcategory = category.filter(el =>el._id === value)
        setsubcategory(rowcategory[0]?.subcategories)
    }
  
}

const handelsubmit = (event) => {
    event.preventDefault()
   
    hideSideBar()
    moveads(Object.assign(state , {id:rowdata._id}))
    .then(res => {toast(res.data.message) ;
        refrechdata()})
    .catch(err => toast(err.data.message))
}
    return (
        <div className="containerform mt-2 p-2">
        <form onSubmit={handelsubmit}>
        <div className="form-group">
        <h6>{t("changeads.formupdate")}</h6> 
            <label>{t("select.category")} </label>
            <select name="category" onChange={handelchange} className="form-control">
                <option value="">{t("select.category")}</option>
                {
                    category.map((el,index) => {return (
                        <option  key={index} value={el._id}> {el.title[i18n]}</option>
                    )})

                    }
            </select>
            </div>
            <div className="form-group">
            <label>{t("select.subcategory")} </label>
            <select name="subcategory" onChange={handelchange} className="form-control">
                <option value="">{t("select.subcategory")}</option>
                
                {
                    subcategory.map((el,index) => {return (
                        <option  key={index} value={el._id}> {el.title[i18n]}</option>
                    )})

                    }
            </select>
            </div>
            <div className="form-group">
            <label>{t("select.region")} </label>
            <br/>
            <select name="region" onChange={handelchange} className="form-control" >
                <option value="">{t("select.region")}</option>
                {
                    region.map((el,index) => {return (
                        <option  key={index} value={el._id}> {el.title[i18n]}</option>
                    )})

                    }
                    
                
            </select>
           </div >
           <div className="form-group">
           <label>{t("select.type")} </label>
           <br/>
            <select name="type" onChange={handelchange} className="form-control">
                <option value="">{t("select.type")}</option>
                {
                    type.map((el,index) => {return (
                        <option  key={index} value={el._id}> {el.title[i18n]}</option>
                    )})

                    }
            </select>
           </div> 
            <div className="group-button d-flex justify-content-around">
    <button
      type="reset"
      className="btn btn-light custom-btn-cancel"
      onClick={() => hideSideBar(false)}
    > {t("form.btncancel")}
     
    </button>
    <input
      value= {t("from.btnadd")}
      type="submit"
      className="btn btn-primary custom-btn-save"
    />
  </div>
        </form>
        </div>
    )
}
