import React , {useEffect,useState} from 'react'
import * as Yup from "yup";
import { useForm } from "react-hook-form";
import { t,getLang } from '../../utils/helper'
import { yupResolver } from "@hookform/resolvers/yup";
import { toast } from 'react-toastify'
import {admin_update_Category} from '../../services/categoriesService'
import {getmaincat} from '../../services/maincategory'
export default function Formupdate({hideSideBar,rowdata,refrechdata}) {
  const [maincat, setshowmaincat] = useState([]);
  const   i18n  = getLang();
  const validationSchema = Yup.object().shape({
        name: Yup.string().required(t("form.champsrequird")
        ),
        mainCategoryId: Yup.string().required(t("form.champsrequird")),
        code: Yup.string().required(
          t("form.champsrequird")
        ),
        requiresApproval : Yup.string().required(t("form.champsrequird"))
      
      });
      const {
        register,
        handleSubmit,
        reset,
    
        formState: { errors },
      } = useForm({
        resolver: yupResolver(validationSchema),
        defaultValues: "",
      });
      useEffect(() => {
        getmaincategorydata()
      reset({
        name:rowdata.title?.ar,
        code:rowdata.title?.en,
        mainCategoryId:rowdata.mainCategory,
        requiresApproval:rowdata.requiresApproval
      })
      }, [reset, rowdata.mainCategory, rowdata.requiresApproval, rowdata.title?.ar, rowdata.title?.en])
      const getmaincategorydata =() =>{
        getmaincat()
          .then(res => {
              if(res.data.success){
              setshowmaincat(res.data.data) 
            }})
          }  
      const onSubmit = async (data) => {
     
        let formData = new FormData();
        formData.append('title[ar]', data.name);
        formData.append('title[en]', data.code);
        formData.append('mainCategoryId', data.mainCategoryId);
        formData.append('requiresApproval',data.requiresApproval);
        admin_update_Category(rowdata._id,formData)
        .then(res => {refrechdata()
          hideSideBar()
          toast.success(res.data.message)

        })
      }
    return (
        <div className="containerform">
             <form onSubmit={handleSubmit(onSubmit)}>
           <h6>{t("category.formupdate")}</h6> 
                <div className="form-group">
    <label className="client-label">
    {t("maincategory")}
    </label>


   <select
   
      name="mainCategoryId"
      placeholder= {t("maincategory")}
      {...register("mainCategoryId")}
      className={`form-control custom-input-org ${
        errors.name ? "is-invalid" : ""
      }`}
    >
        <option value="">    {t("category.catlist")}</option>
      {
        maincat.map((el,index) => 
          <option key={index} value={el._id}>{el.title[i18n]}</option>
        )
      }
      </select> 
    <div className="invalid-feedback">
    {errors.mainCategoryId && <i className="fal fa-info-circle mr-1"></i>}
      {errors.mainCategoryId?.message}
    </div>
  </div>
           <div className="form-group">
    <label className="client-label">
    {t("category.namear")}
    </label>

    <input
   
      name="name"
      type="text"
      placeholder= {t("category.namear")}
      {...register("name")}
      className={`form-control custom-input-org ${
        errors.name ? "is-invalid" : ""
      }`}
    />
    <div className="invalid-feedback">
    {errors.name && <i className="fal fa-info-circle mr-1"></i>}
      {errors.name?.message}
    </div>
  </div>
  <div className="form-group">
    <label className="client-label">
    {t("category.nameen")}
    </label>

    <input
   
      name="code"
      type="text"
      placeholder= {t("category.nameen")}
      {...register("code")}
      className={`form-control custom-input-org ${
        errors.name ? "is-invalid" : ""
      }`}
    />
    <div className="invalid-feedback">
    {errors.name && <i className="fal fa-info-circle mr-1"></i>}
      {errors.name?.message}
    </div>
  </div>
  <div className="form-group">
    <label className="client-label">
    {t("category.requiresApproval")}
    </label>

    <select
      name="requiresApproval"
      {...register("requiresApproval")}
      className={`form-control custom-input-org ${
        errors.requiresApproval ? "is-invalid" : ""
      }`}
    >
       <option value="">    {t("category.selectrequiresApproval")}</option>
       <option value="true">    {t("category.requiresApproval")}</option>
       <option value="false">    {t("category.nnrequiresApproval")}</option>
      </select>
    <div className="invalid-feedback">
    {errors.requiresApproval && <i className="fal fa-info-circle mr-1"></i>}
      {errors.requiresApproval?.message}
    </div>
  </div>
  <div className="group-button d-flex justify-content-around">
    <button
      type="reset"
      className="btn btn-light custom-btn-cancel"
      onClick={() => hideSideBar(false)}
    > {t("form.btncancel")}
     
    </button>
    <input
      value= {t("form.btnupdat")}
      type="submit"
      className="btn btn-primary custom-btn-save"
    />
  </div>
        </form>
        </div>
    )
}
