import { useContext } from 'react';

import {LayoutContext} from '../../contexts/LayoutContext';
import leftArrow from '../../assets/Group_caret.png';

import './MobileMenu.scss';

const MobileMenu = props => {

const {setSubCategory} = useContext(LayoutContext);

  const showHideMobileMenu = e => {
    props.setShowFilters(true)
    props.setShowMobileMenu(show => !show);
    setSubCategory(e.target.innerText);
    props.setAdsQuery(query => ({ ...query, category: e.target.id}));
  }

  const menuItems = props.groupedSubCategories.map(item => (
    <div className="category-subCats">
      <sup>{item.title.en}</sup>
      <div className="subCats">
        {item.categories.map(subCat => (
          <div
            className="subcat-item"
            onClick={showHideMobileMenu}
          >
            <img
              id={subCat._id}
              src={subCat.image}
              alt={`topSales ${subCat.value}`}
            />
            <div id={subCat._id} className="item-value">
              {subCat.title.en}
            </div>
            <img className="left-arrow" src={leftArrow} alt="topsale" />
          </div>
        ))}
      </div>
    </div>
  ));

  return (
    <div className='mobile-sidebar'>
      <i className="fa fa-times" onClick={props.showHideMobileMenu} />
      <h2>Categories</h2>
      {menuItems}
    </div>
  );
};

export default MobileMenu;
