import './AdsFilters.scss';

const AdsFilters = props => {
  const handleSearch = e => props.setAdsQuery(query => ({...query, keyword: e.target.value}));
  return (
    <div className="ads-filters">
      <div className="search">
        <input
          type="text"
          onChange={handleSearch}
          placeholder={`search in ${props.category}`}
        />
        <i className="fa fa-search" />
      </div>

      <button id="sort" onClick={props.handleModal} className="sort">
        Sort
        <i class="fas fa-sort" />
      </button>
      <button id="filter" onClick={props.handleModal} className="filter">
        Filters
        <i class="fas fa-sliders-v" />
      </button>
    </div>
  );
}

export default AdsFilters;
