import React, { Component } from "react";
import { Map, TileLayer, Marker, Popup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import markerIconPng from "leaflet/dist/images/marker-icon.png"
import {Icon} from 'leaflet'
import { t } from '../utils/helper'

const initialState = {
  zoom: 20,
  updated: false
};


export default class MapDisplay extends Component {
  render() {
    if (this.props.lon && this.props.lon.coordinates) {
      const position = [this.props.lon.coordinates[0],this.props.lon.coordinates[1]];
      return (
        <Map
        center={position}
          zoom={initialState.zoom}
          style={{ height: "400px", margin: "15px 0"  }}
         
        >
          <TileLayer
            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          <Marker position={position} icon={new Icon({iconUrl: markerIconPng, iconSize: [25, 41], iconAnchor: [12, 41]})}>
       
          <Popup>
      <h2 className="leaflet-popup-title">{this.props.titre}</h2>

      {/*<img src={'../../image/'+this.props.image} className="leaflet-popup__image" width="70px"  height="70px"/>*/}

      <p> {this.props.description}</p>

     
     <span>{t("price_map")} : {this.props.price}</span>
    </Popup>
    </Marker>
        </Map>
      );
    } else {
      return <h4>Loading Map. Please wait.</h4>;
    }
  }
}