import { getHeaders } from './headers'
import { api } from '../config/axiosConfig'

export const getComments = async (adId, page = 1) => {
  const { data: res } = await api.get(`/api/ads/${adId}/comments`, {
    headers: getHeaders(),
    params: { page },
  })

  const success = res.success
  const message = res.message
  if (success === false) throw Error(message)

  const comments = res.data && res.data.comments
  return comments || []
}

export const PostComments = async (obj) =>  api.post(`/api/comments`, obj,{
    headers: getHeaders(),
  })