import {useState} from 'react';
import Select from 'react-select';

import MaxToMinInut from '../../common/maxToMin/MaxToMin'; 
import {Subcategories, REAL_ESTATE} from '../../../constants/categories';
import R_arrow from '../../../assets/R_arrow.png';

import './Filter.scss'

const Filter = props => {
  const [selectCat, setSelectCat] = useState(false);
  const [selectReg, setSelectReg] = useState(false);
  const [selectSubCat, setSelectSubCat] = useState(false);
  const [filterState, setFilterState] = useState({})

  const [error, setError] = useState();

  const handleSubCategory = (e) =>
    setFilterState(currState => ({...currState, category: e.value}));

  const handlefilterState = (e) => {
    setFilterState(currState => ({...currState, [e.target.id]: e.target.value }))
  }

  const applyFilter = () => {
    if (error && Object.keys(error).length) return;
    props.setAdsQuery(filterState);
    props.closeFilter();
  }
  const categoriesOptions = Object.keys(Subcategories).map(category => ({
    label: category,
    value: category,
  }));
  const subCategoriesOptions = Subcategories[props.category?.value]?.map(subCategory => ({
    label: subCategory.value,
    value: subCategory.id,
  }));

  const regions = [
    {label: 'Al Dawhah ', value: 'Al Dawhah '},
    {label: 'Al Rayyan', value: 'Al Rayyan'},
    {label: 'Al Daayen', value: 'Al Daayen'},
    {label: 'Umm Salal', value: 'Umm Salal'},
    {label: 'Al Khor', value: 'Al Khor'},
  ];
  return (
    <div className="filter-modal">
      <div className="btn-group">
        <button onClick={props.closeFilter}>clear</button>
        <button onClick={applyFilter}>Apply</button>
      </div>
      <div className="title">Filter</div>
      <div className="category">
        <span>Categories</span>
        <div className="category-value">
          <img
            className={selectCat ? 'rotated' : ''}
            src={R_arrow}
            alt="category"
          />
          <Select
            className="select"
            placeholder={props.category?.label}
            onMenuOpen={() => setSelectCat(true)}
            onMenuClose={() => setSelectCat(false)}
            onChange={props.setCategory}
            options={categoriesOptions}
          />
        </div>
      </div>
      <div className="category">
        <span>Sub category</span>
        <div className="category-value">
          <img
            className={selectSubCat ? 'rotated' : ''}
            src={R_arrow}
            alt="subCategory"
          />
          <Select
            className="select"
            placeholder="select a Subcategory"
            onMenuOpen={() => setSelectSubCat(true)}
            onMenuClose={() => setSelectSubCat(false)}
            onChange={handleSubCategory}
            options={subCategoriesOptions}
            isDisabled={!props.category}
          />
        </div>
      </div>
      <div className="range">
        <span>Price range</span>
        <MaxToMinInut
          title="Price"
          onChange={handlefilterState}
          value={filterState}
          error={error}
          setError={setError}
        />
      </div>
      <div className="category">
        <span>Region</span>
        <div className="category-value">
          <img
            className={selectReg ? 'rotated' : ''}
            src={R_arrow}
            alt="region"
          />
          <Select
            className="select"
            placeholder="Select Region..."
            onMenuOpen={() => setSelectReg(true)}
            onMenuClose={() => setSelectReg(false)}
            onChange={props.handleRegionSelection}
            options={regions}
          />
        </div>
      </div>
      {props.adsQuery?.category === Subcategories[REAL_ESTATE][1].id && (
        <>
          <div className="range">
            <span>Rooms</span>
            <MaxToMinInut
              title="Rooms"
              onChange={handlefilterState}
              value={filterState}
              error={error}
              setError={setError}
            />
          </div>
          <div className="range">
            <span>Toilets</span>
            <MaxToMinInut
              title="Toiles"
              onChange={handlefilterState}
              value={filterState}
              error={error}
              setError={setError}
            />
          </div>
        </>
      )}
    </div>
  );
}

export default Filter;
