export const sortingOptions = {
  Newest: 'Newest (default)',
  Closest: 'Closest',
  AscPrice: 'Price: Low to high',
  DescPrice: 'Price: high to low',
  Model: 'Model: Newest',
  Mileage: 'Mileage: Lowest'
}

export const sortingMethods = {
  undefined: () => {},
  'Closest': (ads) => ads,
  'Newest (default)': (ads) => { return ads.sort((a, b) => new Date(a.publishedAt) - new Date(b.publishedAt))},
  'Price: Low to high': (ads) => { return ads.sort((a, b) => a.price - b.price)},
  'Price: high to low': (ads) => { return ads.sort((a, b) => b.price - a.price)},
  'Model: Newest': (ads) => { return ads.sort((a, b) => b.carYear - a.carYear)},
  'Mileage: Lowest': (ads) => { return ads.sort((a, b) => a.km - b.km)},
}