import { sortingOptions, sortingMethods } from '../../../constants/sortingOptions';

import './SortModal.scss';

const SortModal = props => {

  const onClickRadio = (event) => {
    props.setSortType(event.target.value);
    const sortedAds = sortingMethods[event.target.value];
    props.setAds(ads => sortedAds(ads));
  }

  const getSortOptions = Object.values(sortingOptions).map(option => {
    if (
      (option === sortingOptions.Mileage ||
      option === sortingOptions.Model) &&
      props.category !== 'Vehicles, Cars & Truks'
    )
     { return; }
      return (
        <div className="option" key={option}>
          <label htmlFor={option}>{option}</label>
          <input
            onClick={onClickRadio}
            type="radio"
            value={option}
            name="sortingOption"
          />
        </div>
      );
    });

  return (
    <div key="sort" className="sort-modal">
      <button onClick={props.handleModal}>Close</button>
      <div className="title">Sort</div>
      <div className="options" >{getSortOptions}</div>
    </div>
  );
}

export default SortModal;
