import React , {useState , useEffect} from 'react'
import {postonenotification} from '../../services/notofication'
import { Link } from 'react-router-dom';
import * as Yup from "yup";
import { useForm } from "react-hook-form";
import { t ,getLang} from '../../utils/helper'
import { yupResolver } from "@hookform/resolvers/yup";
import {getalluser} from '../../services/userService';
import { toast } from 'react-toastify'
import Select from 'react-select'
import {getadminads} from '../../services/adsService'
import {adminCategory} from '../../services/categoriesService'
export default function Createusernotice() {
  const   i18n  = getLang();
    const [type  , settype] = useState("")
    const [listeads, setlistads] = useState([]); 
    const [mainads , setmainads] = useState([])
    const  [userstate, setuserstate] = useState([])
    const [category, setcategory] = useState([]);
    const validationSchema = Yup.object().shape({
      ad:Yup.string(),
      url:Yup.string(),
      cccategory:Yup.string(),
        ar_message: Yup.string().required(t("form.champsrequird")
        ),
    
        en_message: Yup.string().required(
          t("form.champsrequird")
        ),
        user: Yup.string().required(
            t("form.champsrequird")
          ),
      });
      const {
        register,
        handleSubmit,
        // reset,
        setValue,
        // control,
    
        formState: { errors },
      } = useForm({
        resolver: yupResolver(validationSchema),
        defaultValues: "",
      });

      useEffect(() => {
        getalluser()
        .then(res => {if(res.data.success){
            setuserstate(res.data.data.users)
        }})
        getadminads()
        .then(res=> {
          if(res.data.success){
          setlistads(res.data.data.ads)
          setmainads(res.data.data.ads)
        }
      })
      adminCategory()
      .then(res=> {
          if(res.data.success)
          setcategory(res.data.data)
      })
      }, [])
      const onSubmit = async (data) => {
        //var formData = new FormData("image");
        postonenotification(data)
        .then(res => {toast.success(res.data.message);
          console.log(res.data);
         })

    }
    const  categohandelchange = (selectedOption) => {
     
      setValue('cccategory', selectedOption.value);
      };
      const categoryoption = [{value: '', label: t("select.category") }];
      category.map(el =>categoryoption.push({
        value:el._id , label:el.title[i18n] 
      }) )
      const categohandelchanges = (selectedOption) => {
        if(selectedOption.value !== ''){
          const filtredcat = mainads.filter(el =>el.category._id ===selectedOption.value)
          setlistads(filtredcat)
        }
       else {
        setlistads(mainads)
       }
      }
    return (
        <div>
             <div className="container mt-5">

    <h3>{t("notice.title_onenotice")}</h3>
        <form onSubmit={handleSubmit(onSubmit)}>
        <div className="form-group">
        <label className="client-label">
    {t("notice.listuser")}
    </label>
        <select       name="user"   {...register("user")}
                        className={`form-control custom-input-org `}
                      
                    
                        >
                           <option value="">    {t("notice.listuser")}</option>
                            {userstate.map((el,index) => {
                                return (
                                    <option value={el._id} key={index}>{  el?.name +'('+el.mobile+ ')'}</option>
                                )
                            })}
                        </select>
                        <div className="invalid-feedback">
    {errors.user && <i class="fal fa-info-circle mr-1"></i>}
      {errors.user?.message}
    </div>
                        </div>
        <div className="form-group">
    <label className="client-label">
    {t("notice.arabemessage")}
    </label>

    <input
   
      name="ar_message"
      type="text"
      placeholder= {t("notice.arabemessage")}
      {...register("ar_message")}
      className={`form-control custom-input-org ${
        errors.ar_message ? "is-invalid" : ""
      }`}
    />
    <div className="invalid-feedback">
    {errors.ar_message && <i className="fal fa-info-circle mr-1"></i>}
      {errors.ar_message?.message}
    </div>
  </div>
  <div className="form-group">
    <label className="client-label">
    {t("notice.englishmessage")}
    </label>

    <input
   
      name="en_message"
      type="text"
      placeholder= {t("notice.englishmessage")}
      {...register("en_message")}
      className={`form-control custom-input-org ${
        errors.en_message ? "is-invalid" : ""
      }`}
    />
    <div className="invalid-feedback">
    {errors.en_message && <i className="fal fa-info-circle mr-1"></i>}
      {errors.en_message?.message}
    </div>
  </div>
  <div className="form-group">
  <label className="client-label">
    {t("notice.type")}
    </label>
    <select onChange={(e)=>settype(e.target.value)}>
    <option value=""></option>
      <option value="url">url</option>
      <option value="ads">ads</option>
      <option value="category">category</option>
    </select>
    </div>
    {type === 'ads'? 
  <div className="form-group">
    <label className="client-label">
    {t("notice.ad")}
    </label>
    <Select
                name="catego"
         className="col-md-12 "
       onChange={categohandelchanges}
       options={categoryoption}
     />
     <br/>
    <select
   
      name="ad"
      type="text"
      placeholder= {t("notice.ad")}
      {...register("ad")}
      className={`form-control custom-input-org `}
    >
   <option value="">    {t("notice.addall_form.ads")}</option>
                              {
                                listeads.map((el,index) => {
                                  return (
                                      <option value={el._id} key={index}>{ el?.title}</option>
                                  )
                              })
                              }
        </select>
  </div>
  :type === 'url' ?
  <div className="form-group">
    <label className="client-label">
    {t("notice.url")}
    </label>

    <input
   
      name="url"
      type="text"
      placeholder= {t("notice.url")}
      {...register("url")}
      className={`form-control custom-input-org`}
    />
 
  </div> :type === 'category'?
  <>
   <label className="client-label">
    {t("notice.category")}
    </label>
    <Select
                name="catego"
         className="col-md-12 "
       onChange={categohandelchange}
       options={categoryoption}
     />
    {/* <select 
                        className={`form-control`}
                        {...register("cccategory")}
                          name="cccategory"
                        >
                           <option value="">    {t("category.catlist")}</option>
                            {category.map((el,index) => {
                                return (
                                    <option value={el._id} key={index}>{getLang()==="ar" ? el?.title?.ar : el?.title?.en}</option>
                                )
                            })}
                        </select> */}
  </>
  : ""
}
  <div className="row">
  <input
      value= {t("from.btnadd")}
      type="submit"
      className="btn btn-primary custom-btn-save col-md-2 ml-1 mr-1"
    />
   <Link to="/admin/notification" className="btn btn-danger col-md-2 "> <button
      type="reset"
      className="btn btn-danger col-md-2 ml-1 mr-1"
    > {t("form.btncancel")}
     
    </button>
    </Link>
  </div>
            </form>
            </div>
        </div>
    )
}
