import axios from 'axios'
import { getHeaders } from './headers'

export const getCarmode = async (id) => {
    const { data: res } = await axios.get(`api/admin/car-makes/${id}/car-models`, {
      headers: getHeaders(),
    })
  
    const success = res.success
    const message = res.message
    if (success === false) throw Error(message)
  
    const model = res.data 
    return   model
  }
  export const updatecarmodel = async (don) => {
    const {data} = await axios.put(`/api/admin/car-models/${don.id}`,don,{headers:getHeaders()})
    const success = data.success
    const message = data.message
    if(success === false) throw Error(message)
    return {success,message}
      }
  export const postCarmode = async (don) => {
      const { data: res } = await axios.post('/api/admin/car-models/',don, {
        headers: getHeaders(),
      })
      const success = res.success
    const message = res.message
    if (success === false) throw Error(message)
  
    return { success, message }
    }
    export const deleteCarmode = async (id) => {
        const {data} = await axios.delete(`/api/admin/car-models/${id}`,{headers:getHeaders()})
        const success = data.success
        const message = data.message
        if(success === false) throw Error(message)
        return {success,message}
          }