import React, {useState, useEffect, useContext } from 'react';

import AdCard from './adCard/AdCard';

import { getAds } from '../../services/adsService';
import AdsFilters from './adsFilters.jsx/AdsFilters';
import { LayoutContext } from '../../contexts/LayoutContext';
import Filter from './modals/Filter';
import SortModal from './modals/SortModal';

import './HomePage.scss'
import Loader from '../Loader';

const HomePage = () => {
  const [ads, setAds] = useState(null);
  const [offset, setOffset] = useState(1);
  const [modal, setModal] = useState(false);
  const [sortType, setSortType] = useState();
  const [loadingAds, setLoadingAds] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [maxMinPrices, setMaxMinPrices] = useState({ max: '', min: '' })

  const MAX = 'maxprice';
  const MIN = 'minprice';

  const {
    category,
    searchText,
    setCategory,
    showFilters,
    subCategory,
    setSubCategory,
    adsQuery,
    setAdsQuery,
  } = useContext(LayoutContext);
  const screenWidth = window.screen.width;
  const [perPage] = useState(() => {
    if (screenWidth > 1500) {
      return 8;
    } else if ((screenWidth <= 1500 && screenWidth > 1200) || screenWidth <= 920) {
      return 6;
    } else if (screenWidth <= 1200 && screenWidth >= 921) {
      return 4;
    }
  });

  useEffect(() => {
    const pageInit = async () => {
      const ads = await getAds(offset, {perPage});
      setAds(ads);
      setOffset(offset + 1);
      setLoadingAds(false);
    };
    pageInit();
  }, []);

  useEffect(() => {
    const getAdsByQuery = async () => {
      setLoadingAds(true);
      const ads = await getAds(1, {
        perPage,
        ...adsQuery
      });
      setAds(ads);
      setLoadingAds(false);
    };

    getAdsByQuery();
  }, [adsQuery, perPage]);

  const getMoreAds = async () => {
    setLoadingAds(true)
    const newAds = await getAds(offset, {
      perPage,
      ...adsQuery,
    });
    setAds(ads =>
      [
        ...ads,
        ...newAds
      ]);
      setLoadingAds(false);
      setOffset(offset + 1);
  }
  
  const adsCards =
    ads?.length &&
    ads?.map(({title, photos, price, _id}) => (
      new RegExp(searchText, 'i').test(title)&&
      <AdCard
        style={{ marginRight: '46px' }}
        key={Math.random() * 10000}
        title={title}
        price={price}
        image={photos[0]?.org}
        id={_id}
      />
    ));

    const handleModal = e => {
      setShowModal(!showModal);
      if (e) setModal(e.target.id);
    }

    const handleRegionSelection = (selectedRegion) => {
      setAds(currentAds =>
        currentAds.filter(ad => ad.region?.title?.en === selectedRegion.value)
      );
    };

    const handleCategorySelection = selectedcategory => {
      setAds(currentAds =>
        currentAds.filter(
          ad => ad.category?.title?.en === selectedcategory.value
        )
      );
    };

    const handlePriceChange = e => {
      if (e.target.id === MAX)
        setMaxMinPrices({...maxMinPrices, max: e.target.value});
      if (e.target.id === MIN)
        setMaxMinPrices({...maxMinPrices, min: e.target.value});
      if (maxMinPrices.max && maxMinPrices.min)
        setAds(currentAds =>
          currentAds.filter(
            ad =>
              ad.price <= maxMinPrices.max &&
              ad.price <= maxMinPrices.max
          )
        );
    };

  return (
    <>
      {showModal && (
        <div className="global-overlay">
          {modal === 'sort' && (
            <SortModal
              handleModal={handleModal}
              setSortType={setSortType}
              category={category}
              setAds={setAds}
            />
          )}
          {modal === 'filter' && (
            <Filter
              category={category}
              adsQuery={adsQuery}
              setCategory={setCategory}
              subCategory={subCategory}
              setAdsQuery={setAdsQuery}
              closeFilter={handleModal}
              maxMinPrices={maxMinPrices}
              setSubCategory={setSubCategory}
              handlePriceChange={handlePriceChange}
              handleRegionSelection={handleRegionSelection}
              handleCategorySelection={handleCategorySelection}
            />
          )}
        </div>
      )}
      <div className="home-page">
        {showFilters && (
          <div className="filter-sort-categories">
            <AdsFilters
              category={subCategory}
              handleModal={handleModal}
              setAdsQuery={setAdsQuery}
            />
          </div>
        )}
        {!showFilters && !loadingAds && <div className="location-sign" />}
        {ads?.length ? (
          <div className="cards">{adsCards}</div>
        ) : (
          !loadingAds && (<h4>No Ads Found !</h4>)
        )}
        <Loader isLoading={loadingAds}>
          <button className="view-more" onClick={getMoreAds}>
            View more
          </button>
        </Loader>
      </div>
    </>
  );
}

export default HomePage;
