import React , {useEffect,useState} from 'react'
import ReactPaginate from "react-paginate";
import {Modal} from 'react-bootstrap'
import {gettypes,deletetypes} from '../../services/typesServices'
import { t ,getLang} from '../../utils/helper'
import {useParams} from "react-router-dom";
import Formadd from './typesadd'
import FormUpdate from './typesupdate'
import { toast } from 'react-toastify'
export default function Types() {
    let { id } = useParams();
    const   i18n  = getLang();
    const [dateshow, setdateshow] = useState([])
    const [rowsPerPage , setrowsPerPage] = useState([])

    let filterval = [];
    // const [filterval , setfilterval] = useState([])
    const [offset , setoffset] = useState(0)
    const [selected , setselected] = useState(0)
    
    let pagelimit = 5;
    // const [pagelimit,setpagelimit] = useState(5)
    const [droweradd , setadddrower] = useState(false)
    const [drowerupdate , setdrowerupdate] = useState(false)
    const [selectedel , setselectedel] = useState("")
    const [showdelect , setshowdelect] = useState(false)
    const [updatedata , setupdatedata] = useState('')
    useEffect(() => {

      const gettypedata = async () => {
          const row = await gettypes(id)
          const dats =  row.slice(offset, pagelimit+offset)
          setrowsPerPage(row)
          if(Boolean(dats.length))
         setdateshow(dats)
         else {
           const dat =  row.slice(0, pagelimit) 
           setselected(0)
           setdateshow(dat)
         }
         
      }

      gettypedata()
    }, [id , offset , pagelimit])

      const gettypedata = async () => {
          const row = await gettypes(id)
          const dats =  row.slice(offset, pagelimit+offset)
          setrowsPerPage(row)
          if(Boolean(dats.length))
         setdateshow(dats)
         else {
           const dat =  row.slice(0, pagelimit) 
           setselected(0)
           setdateshow(dat)
         }
         
      }
    const  onPageChanged = (data) => {
        let selected = data.selected;
      let offset = Math.ceil(selected * pagelimit);
      if(Boolean(filterval.length))
      { const currentdata = filterval.slice(offset, offset +pagelimit)
       setoffset(offset)
       setselected(selected)
      setdateshow(currentdata)
      }
      else{
        const currentdata = rowsPerPage.slice(offset, offset +pagelimit)
        setoffset(offset)
        setselected(selected)
       setdateshow(currentdata)
       }
      }
      const Updaterow = (el) => {
        setupdatedata(el)
        setdrowerupdate(true)
      }
      const detailrow = (el) => {
        setselectedel(el)
        setshowdelect(true)
      }
      const deletecatrowsuprission = async () => {
        try{
        const deleterow = await deletetypes(selectedel._id)
        if(deleterow.success)
        {
          gettypedata()
          setshowdelect(false)
        }
      }
      catch(err){
        setshowdelect(false)
        toast.error(err.message)
      }
     
      }
      const hidedrower = () => {
        if(droweradd)
        setadddrower(false)
      }
      

    return (
        <div className="container mt-3">
              <button onClick={() =>setadddrower(true)} className="addnew btn btn-primary mr-2 ml-2"><i class="fal fa-plus pl-2 pr-2"></i>{" "} {t("Admin.btnadd")}</button>
              <div onClick={() => hidedrower()} className={droweradd ?"overlay is-open":"overlay"}></div>
            <div className={(droweradd || drowerupdate) &&i18n==="ar"   ? "arnav-menu  active" :(droweradd || drowerupdate )&&i18n==="en" ?"nav-menu  active": "nav-menu"}>
        {drowerupdate &&  <FormUpdate  hideSideBar={setdrowerupdate} rowdata ={updatedata} refrechdata= {gettypedata}  /> }
       {droweradd && <Formadd  hideSideBar={setadddrower} refrechdata={gettypedata} idcatego={id}/> }
       </div>
             <div dir="ltr" className= {!Boolean(dateshow.length) ? "d-none float-right mt-2" :"float-right mt-2"}>
             <ReactPaginate
          previousLabel={<i className="fal fa-chevron-left"></i>}
          nextLabel={<i className="fal fa-angle-right"></i>}
          breakLabel={'...'}
          breakClassName={'break-me'}
          pageCount={filterval.length >0 ?Math.ceil(filterval.length / pagelimit) : Math.ceil(rowsPerPage.length / pagelimit)}
          marginPagesDisplayed={2}
          pageRangeDisplayed={2}
          onPageChange={onPageChanged}
          containerClassName={'pagination'}
          activeClassName={'active'}
          initialPage={0}
          forcePage={selected}
        /> </div>
           <div className="table-responsive">
             {rowsPerPage.length  > 0 ? 
             <table className="table table-borderless mt-4">
          <thead>
            <tr className=" th-table">
              <th className="col ">{t("type.id")}</th>
              <th className="col">{t("type.namear")}</th>
              <th className="col">{t("type.namean")}</th>
              <th className="col">{t("type.status")}</th>
              <th className="col">{t("type.action")}</th>
              
            </tr>
          </thead>

          <tbody>
            {" "}
           
            {
              dateshow.map((el, index) => {
                return (
                  <tr key={index} className="row custom-tr ">
                    <td label={t("type.id")} className="col">{el._id.slice(el._id.length - 3,el._id.length)}</td>
                    <td label={t("type.namear")} className="col">{el?.title?.ar}</td>
                    <td label={t("type.namean")} className="col">{el?.title?.en }</td>
                    <td label={t("type.status")} className="col">{el?.title?.en }</td>
                    
                   
                    <td
                      label={t("type.action")}
                      className="col"

      
        >
          <i className="fas fa-pen cursor-pointer" onClick={() =>Updaterow(el)}></i> <i  onClick={() =>detailrow(el)} className="fas fa-trash-alt tabfonticon pr-2 pl-2 cursor-pointer"
                        ></i></td>
                  </tr>
                );
              })}{" "}
          </tbody>
        </table>
        : <p className="m-5"> {t("type.nestpasencore")} </p> }
        </div> 
        <Modal show={showdelect} onHide={() =>setshowdelect(false)}>
<Modal.Header closeButton>
        <Modal.Title>{t("model.titel")} {" "} {i18n === "ar" ? selectedel?.title?.ar : selectedel?.title?.en }</Modal.Title>
      </Modal.Header>

      <div className="modal-body">
        {t("model.body")}
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-secondary p-2" onClick={() =>setshowdelect(false)}>{t("modal.cancel")}</button>
        <button type="button" className="btn btn-primary p-2" onClick={() =>deletecatrowsuprission()}>{t("modal.delete")}</button>
      </div>
    </Modal>
        </div>
    )
}
