import React, { useState, useContext } from 'react';

import './DropDownMenu.scss'
import { LayoutContext } from '../../../contexts/LayoutContext';
const DropDownMenu = props => {
  const { setShowFilters, setCategory, setSubCategory, setAdsQuery} = useContext(LayoutContext);
  const [showMenu, setShowMenu] = useState(false);

  
  const handleMenu = (e) => {
    setShowMenu(!showMenu);
    if(!showMenu) {
      e.target.style.backgroundColor = '#880d4e';
      e.target.style.color = 'white';
    } else {
      e.target.style.backgroundColor = 'white';
      e.target.style.color = 'black';
    }
  }

  const handleOnBlur = (e) => {
    if(showMenu) handleMenu(e);
    return; 
  }

  const handleItems = (item) => {
    setShowFilters(true);
    setCategory(props.title);
    console.log(item)
    setSubCategory(item.value)
    setAdsQuery(query => ({ category: item.id }));
  };

  const renderMenuItems = items => {
    if(!Array.isArray(items)) return;
    return (
      <ul>
        {items.map((item) => (
          <li key={Math.random()} onMouseDown={() => handleItems(item)}>
            <img
              src={item.iconSrc}
              alt={`topSales ${item.value}`}
            />
            <div className="item-value">{item.value}</div>
          </li>
        ))}
      </ul>
    );
  }

  return (
    <div className="menu" key={props.menuIndex * Math.round()}>
      <button
        onClick={handleMenu}
        onBlur={handleOnBlur}
      >
        {props.title}
      </button>
      <div className="menu-items">
        {showMenu ? renderMenuItems(props.menuItems) : null}
      </div>
    </div>
  );
}

export default DropDownMenu;