import React, {Component} from 'react';
import Slider from 'react-slick';

import {getgrouped} from '../../../services/categoriesService';
import Rnav from '../../../assets/R-caret.png';
import Lnav from '../../../assets/L-caret.png';

import './Slider.scss';

function NextArrow(props) {
  const {className, onClick} = props;
  return (
    <div className={className}>
      <button className="btn-next-arrow" onClick={onClick}>
        <img src={Lnav} alt="left-nav" />
      </button>
    </div>
  );
}

function PrevArrow(props) {
  const {className, style, onClick} = props;
  return (
    <div className={className}>
      <button className="btn-prev-arrow" onClick={onClick}>
        <img src={Rnav} alt="left-nav" />
      </button>
    </div>
  );
}

export default class CustomArrows extends Component {
  constructor() {
    super();
    this.state = {
      subCategories: []
    }
  }

  async componentDidMount() {
    const subcategoriesList = await getgrouped();
     const subCategories = subcategoriesList.data.data.reduce((acc, el) =>{
      acc.push(...el.categories);
      return acc;
    }, []) 

   this.setState({subCategories}, () => {
   });
  }

  getSlidesToShow() {
    const windowWidth = window.screen.width;
   
    if (windowWidth > 870) return 4;
    if (windowWidth <= 870 && windowWidth > 650) return 3.5;
    else return 3;
  }

  render() {
    const settings = {
      infinite: true,
      slidesToShow: this.getSlidesToShow(),
      slidesToScroll: 2,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    };

    const handleSubCatClick = (e) => {
      this.props.setAdsQuery(query => ({ category: e.target.id}));
      this.props.setSubCategory(e.target.name)
      this.props.setShowFilters(true);
    }

    const slides = this.state.subCategories.map(subCat => (
      <div className="ad-card subCat">
        <img
          id={subCat._id}
          name={subCat.title.en}
          onClick={handleSubCatClick}
          src={subCat.image}
          alt={subCat.title.en}
        />
        <div className="ad-title">
          <div id={subCat._id} className="title" dir="auto">
            {subCat.title.en}
          </div>
        </div>
      </div>
    ));

    return (
      <div className="slider-container">
        <Slider {...settings}>{slides}</Slider>
      </div>
    );
  }
}
