import React from 'react';

import './AdCard.scss'

const AdCard = props => {
 const getAdDetails = () => {
    var win = window.open(`/ads/${props.id}`);
    win.focus();
  };
  return (
    <div onClick={getAdDetails} className="ad-card">
      <img src={props.image} alt={props.title} />
      <div className="ad-title">
        <div className="title" dir="auto">
          {props.title}
        </div>
        <div className="price">{props.price} QAR</div>
      </div>
    </div>
  );
}

export default AdCard;
