import React from 'react';

import DropDownMenu from '../../common/dropDownMenu/DropDownMenu';
import {CategoriesTitles, Subcategories} from '../../../constants/categories';

import './Categories.scss';

const Categories = props => {

  const categoriesMenus = CategoriesTitles.map((categoryTitle, index) => (
    <DropDownMenu
      key={Math.random() * 1000}
      menuIndex={index}
      title={categoryTitle}
      menuItems={Subcategories[categoryTitle]}
    />
  ));
  return <div className='category-menu'>{categoriesMenus}</div>;
}

export default Categories;
