import React,{useEffect , useState} from 'react'
 import {getUser} from '../../services/userService'
 import { Button, Card, Image } from 'react-bootstrap'
 import { avatar } from '../../utils/images'
 import { t , getLang} from '../../utils/helper'
 import AdsList from '../../components/AdsList'
 import EmptyView from '../../components/EmptyView'
 import Loader from '../../components/Loader'
 import { adminfavoriteuserFavorite } from '../../services/favAdsService'
 import { getUserAds } from '../../services/adsService'
 import { usersgetFollowers ,usersgetFollowings  } from '../../services/followService'
 import Follower from '../../components/Follower'
 import {
    useParams
  } from "react-router-dom";
export default function Userinfo() {
    const   i18n  = getLang();
    let { id } = useParams();
    const [user, setuser] = useState()

    let error = "";
    // const [error, setError] = useState('')

    let isLoading = false
    // const [isLoading, setIsLoading] = useState(false)
    const [ads, setads] = useState([])
    const [followers, setfollower] = useState()
    const [Followingss, setFollowings] = useState()
    const [favorites, setfavorite] = useState([])
    const [showpanel, setshowpanel] = useState("")
    useEffect(() => {

        async function getData(){
          const abc = await getUser(id)
          const adsuser =  await getUserAds(id )
          const favorite =  await adminfavoriteuserFavorite(id )
          const follower = await usersgetFollowers(id)
          const Followings = await usersgetFollowings(id)
          setfavorite(favorite)
          setfollower(follower)
          setFollowings(Followings)
          setads(adsuser)
          setuser(abc)
        }

        getData()
      
    }, [id])
    return (
        <div className="container">

<Card className="bg-light p-3 mb-3 border-box-ads mt-3">
    
        <div className="d-flex justify-content-sm-center">
        <Image
            className="shadow-sm w-10 h-10 mb-3"
            roundedCircle
            src={user?.profilePhoto || avatar}
            width={120}
            height={120}
            alt="user profile photo"
          />
            <div>
          <div className="mb-2">
            <strong>{user?.name?user.name : t("without.name") }</strong>
          </div>
          <p> {user?.mobile}</p>
          <p>{user?.joinDate[i18n]} </p>
        
          </div>
        </div>
        <div className="d-flex justify-content-sm-around">
        <Button onClick={() =>setshowpanel('ads')}>{t("user.adsCount")}  {ads.length}</Button>
        <Button onClick={() =>setshowpanel('followings')}> {t("user.followingsCount")} {user?.stats.followingsCount}</Button>
        <Button onClick={() =>setshowpanel('follower')}>{t("user.followersCount")} {user?.stats.followersCount}</Button>
        <Button onClick={() =>setshowpanel('favads')}>{t("user.favAdsCount")} {favorites.length}</Button>
        <Button onClick={() =>setshowpanel('block')}>{t("user.blocksCount")} {user?.stats.blocksCount}</Button>
        </div>
        </Card>
       {showpanel ==="ads" &&
        <Loader isLoading={isLoading}>
      <h3 className="border-bottom pb-3 mb-3">{t('ads')}</h3>
     
      <EmptyView isEmpty={error} message={error || t('no_data')}>
      {ads.length > 0 ?  <AdsList ads={ads} /> : t('nofavorite_ads') } 
      </EmptyView>
    </Loader>
        }
          {showpanel ==="follower" &&
    <Loader isLoading={isLoading}>
      <h3 className="border-bottom pb-3 mb-3">{t('follower')}</h3>
     
      <EmptyView isEmpty={error} message={error || t('no_data')}>
      {followers.length > 0 ? 
        followers.map(el => {
          return  <Follower user={el} /> 
        })
      : t('nofavorite_ads') } 
      </EmptyView>
    </Loader>
         }
         {showpanel ==="followings" &&
    <Loader isLoading={isLoading}>
      <h3 className="border-bottom pb-3 mb-3">{t('followings')}</h3>
     
      <EmptyView isEmpty={error} message={error || t('no_data')}>
      {user?.stats.followingsCount> 0 ?  
      Followingss.map(el => {
        
        return <Follower user={el} /> 
      })
      : t('nofavorite_ads') } 
      </EmptyView>
    </Loader>
         }
         {showpanel ==="favads" &&
    <Loader isLoading={isLoading}>
      <h3 className="border-bottom pb-3 mb-3">{t('favorite')}</h3>
     
      <EmptyView isEmpty={error} message={error || t('no_data')}>
      {user?.stats.favAdsCount> 0 ?  <AdsList ads={favorites} /> : t('nofavorite_ads') } 
      </EmptyView>
    </Loader>
    }
        </div>
    )
}
