import React from 'react'
import * as Yup from "yup";
import { useForm } from "react-hook-form";
// import { t ,getLang} from '../../utils/helper'
import { t } from '../../utils/helper'
import { yupResolver } from "@hookform/resolvers/yup";
import {postCarmode} from "../../services/carmodeService"
import { toast } from 'react-toastify'
export default function UserFromadd({hideSideBar,refrechdata,id}) {
    const validationSchema = Yup.object().shape({
        namear: Yup.string().required(t("form.champsrequird")
        ),
      });
      const {
        register,
        handleSubmit,
        // reset,
        // control,
    
        formState: { errors },
      } = useForm({
        resolver: yupResolver(validationSchema),
        defaultValues: "",
      });
      const onSubmit = async (data) => {

          const rdata = {
            title:data.namear ,
            carMakeId:id,
            carMake:id
          };
          try {
          const result = await postCarmode(rdata)
       if(result)
       {
       toast.success(result.message)
       refrechdata()
          }
    } catch (ex) {
        const errorString = ex.message || ex.toString()
        toast.error(errorString)
       
      }

            
      }
    return (
        <div className="containerform">
        <form onSubmit={handleSubmit(onSubmit)}>
           <h6>{t("carmarkes.titel")}</h6> 
           <div className="form-group">
    <label className="client-label">
    {t("carmarkes.title")}
    </label>

    <input
   
      name="namear"
      type="text"
      placeholder= {t("carmarkes.title")}
      {...register("namear")}
      className={`form-control custom-input-org ${
        errors.name ? "is-invalid" : ""
      }`}
    />
    <div className="invalid-feedback">
    {errors.namear && <i className="fal fa-info-circle mr-1"></i>}
      {errors.namear?.message}
    </div>
  </div>

  <div className="group-button d-flex justify-content-around">
    <button
      type="reset"
      className="btn btn-light custom-btn-cancel"
      onClick={() => hideSideBar(false)}
    > {t("form.btncancel")}
     
    </button>
    <input
      value= {t("from.btnadd")}
      type="submit"
      className="btn btn-primary custom-btn-save"
    />
  </div>
        </form>
        </div>
    )
}
