import React,{useEffect} from 'react'
import * as Yup from "yup";
import { useForm } from "react-hook-form";
import { t } from '../../utils/helper'
import { yupResolver } from "@hookform/resolvers/yup";
import {updateadminuser} from "../../services/userService"
import { toast } from 'react-toastify'
// import {
//   InputGroup,
// } from 'react-bootstrap'
export default function UserFromupdate({hideSideBar,refrechdata,rowdata}) {
    const validationSchema = Yup.object().shape({
      isAdmin: Yup.string().required(t("form.champsrequird")
        ),
    
        isEnabled: Yup.string().required(
          t("form.champsrequird")
        ),
        role: Yup.string().required(
          t("form.champsrequird")
        ),
      
      });
      const {
        register,
        handleSubmit,
        reset,
        // control,
    
        formState: { errors },
      } = useForm({
        resolver: yupResolver(validationSchema),
        defaultValues: "",
      });
      useEffect(() => {
        console.log(rowdata)
        reset({
          isAdmin:rowdata.isAdmin,
          isEnabled:rowdata.isEnabled,
          role:rowdata.role
        })
      }, [reset , rowdata])
      
      const onSubmit = async (data) => {

          const userdata = {
            id:rowdata._id,
            isAdmin:data.isAdmin ,
            isEnabled:data.isEnabled,
            role:data.role

            
          }
          //var formData = new FormData("image");
          updateadminuser(userdata)
          .then(res => {toast.success(res.data.message);
            hideSideBar(false)
             refrechdata()})

      }
    return (
        <div className="containerform">
        <form onSubmit={handleSubmit(onSubmit)}>
           <h6>{t("user.formupdate-titel")}</h6> 
           <div className="form-group">
    <label className="client-label">
    {t("user.formupdate-isadmin")}
    </label>

    <input
   
      name="isAdmin"
      type="text"
      placeholder= {t("user.formupdate-isadmin")}
      {...register("isAdmin")}
      className={`form-control custom-input-org ${
        errors.isAdmin ? "is-invalid" : ""
      }`}
    />
    <div className="invalid-feedback">
    {errors.isAdmin && <i className="fal fa-info-circle mr-1"></i>}
      {errors.isAdmin?.message}
    </div>
  </div>
  <div className="form-group">
    <label className="client-label">
    {t("user.formupdate-isunabel")}
    </label>
   
    <select
   
      name="isEnabled"
      type="number"
      placeholder= {t("user.formupdate-isunabel")}
      {...register("isEnabled")}
      className={`form-control custom-input-org ${
        errors.isEnabled ? "is-invalid" : ""
      }`}
    >
     
    <option value="false">false</option>
    <option value="true">true</option>
    </select>
    <div className="invalid-feedback">
    {errors.isEnabled && <i className="fal fa-info-circle mr-1"></i>}
      {errors.isEnabled?.message}
    </div>
  </div>
  <div className="form-group">
    <label className="client-label">
    {t("user.formupdate-role")}
    </label>

    <select
   
      name="role"
      type="text"
      placeholder= {t("user.formupdate-role")}
      {...register("role")}
      className={`form-control custom-input-org ${
        errors.role ? "is-invalid" : ""
      }`}
    >
    <option value="user">user</option>
    <option value="admin">admin</option>
    <option value="editor">editor</option>
    </select>
    <div className="invalid-feedback">
    {errors.role && <i className="fal fa-info-circle mr-1"></i>}
      {errors.role?.message}
    </div>
  </div>
  <div className="group-button d-flex justify-content-around">
    <button
      type="reset"
      className="btn btn-light custom-btn-cancel"
      onClick={() => hideSideBar(false)}
    > {t("form.btncancel")}
     
    </button>
    <input
      value= {t("form.btnupdat")}
      type="submit"
      className="btn btn-primary custom-btn-save"
    />
  </div>
        </form>
        </div>
    )
}
