import React,{useEffect,useContext,useState} from 'react'
import {arrayMoveImmutable} from "array-move";
import AuthContext from '../../contexts/AuthContext'
import SortableList, { SortableItem } from "react-easy-sort";
import {sortcategorydata,adminCategory} from '../../services/categoriesService'
import { toast } from 'react-toastify'
import { t } from '../../utils/helper'
export default function Sortcategory() {
    const { lang } = useContext(AuthContext)
    const [items, setItems] = useState([]);

useEffect(() => {
    adminCategory()
    .then(res => {
        if(res.data.success){
            setItems(res.data.data)
        }
    })

}, [])
      const onSortEnd = (oldIndex, newIndex) => {
   setItems((array) => arrayMoveImmutable(array, oldIndex, newIndex));
      };
      const savesort = () => {
        const tabsid = items.map(el => el._id)
       const obj = {
            "categories":tabsid 
      }
      sortcategorydata(obj)
      .then(res =>    toast.success(res.data.message))
      .catch(err =>toast.error(err.response.data.message))
    
}

    return (
        <div className="container mt-5">
         <button onClick={savesort} className="btn btn-primary mb-4">{t("sort.savesort")} </button> 
        <SortableList
      onSortEnd={onSortEnd}
      className="list col-md-6"
      draggedItemClassName="dragged"
    >
      {items.map((item) => (
        <SortableItem key={item}>
          <div className="itemsorted">{item?.title[lang]}</div>
        </SortableItem>
      ))}
    </SortableList>
        </div>
    )
}
