import axios from 'axios'
import { getHeaders } from './headers'
export const getrejectedraison = async () => {
    const { data: res } = await axios.get('/api/admin/reject-reasons/all', {
      headers: getHeaders(),
    
    })
  
    const success = res.success
    const message = res.message
    if (success === false) throw Error(message)
  
    const users = res.data && res.data
    return users || []
}  

export const updaterejected = async obj => {
    const { data: res } = await axios.put(
      `/api/admin/reject-reasons/update/${obj.id}`,
      obj,
      {
        headers: getHeaders(),
      }
    )
  
    const success = res.success
    const message = res.message
    if (success === false) throw Error(message)
    return { message, success }
  }