import axios from 'axios'
import { getHeaders } from './headers'

export const gettypes = async (id) => {
    const { data: res } = await axios.get(`/api/admin/categories/${id}/types/`, {
      headers: getHeaders(),
    })
  
    const success = res.success
    const message = res.message
    if (success === false) throw Error(message)
  
    return res.data || []
  }

  export const deletetypes = async (id) => {
    const { data: res } = await axios.delete(`/api/admin/types/${id}`, {
      headers: getHeaders(),
    })
  
    const success = res.success
    const message = res.message
    if (success === false) throw Error(message)
  
    return {success,message}
  }

  export const postetypes = async (date) => axios.post(`/api/admin/types/`,date,{
      headers: getHeaders(),
    })
  export const updatetypes = async(data) => axios.put(`/api/admin/types/${data.id}`,data,{
    headers: getHeaders(),
  }) 

   