import './MaxToMin.scss'; 
import { useEffect } from 'react';


const MaxToMinInput = props => {
  const max = parseInt(props.value[`max${props.title}`], 10);
  const min = parseInt(props.value[`min${props.title}`], 10);
  const checkError = () => {
    if (props.value && min && max && min > max) {
      props.setError(currError => ({
        ...currError,
        [props.title]: `max ${props.title.toLowerCase()} nubmer must be superior than min ${props.title.toLowerCase()} number`,
      }));
    } else if (min <= max) {
      const clonedError = { ...props.error }
      delete clonedError[props.title];
      props.setError(clonedError);
    }
  };

  useEffect(checkError, [
    props.value[`max${props.title}`],
    props.value[`min${props.title}`],
  ]);
  return (
    <div className="max-to-min-container">
      <div className="max-to-min">
        <input
          id={`max${props.title}`}
          type="number"
          onChange={props.onChange}
          placeholder="Max"
        />
        to
        <input
          id={`min${props.title}`}
          type="number"
          onChange={props.onChange}
          placeholder="Min"
        />
      </div>
      {props.error && (
        <div className="error">{props.error[props.title]}</div>
      )}
    </div>
  );
}

export default MaxToMinInput;
