import React , {useEffect,useState} from 'react'
import { t ,getLang} from '../../utils/helper'
import ReactPaginate from "react-paginate";
import FormUpdate from './updatereject'
import {getrejectedraison} from '../../services/rejectedraisonService'

export default function Rejectraison() {
    const lng = getLang()
    const [drowerupdate , setdrowerupdate ] = useState(false)
    const [updatedata , setupdatedata] = useState('')

    let filterval = [];

    // const [filterval , setfilterval] = useState([])
    const [offset , setoffset] = useState(0)
    const [selected , setselected] = useState(0)

    let pagelimit = 5;
    // const [pagelimit,setpagelimit] = useState(5)
    const [dateshow, setdateshow] = useState([])
    const [rowsPerPage , setrowsPerPage] = useState([])

    const getrejecteddata =  async () => {
      const list = await getrejectedraison()
        setrowsPerPage(list) 
        const row = list
          const dat =  row.slice(offset, pagelimit+offset)
          if(Boolean(dat.length))
         setdateshow(dat)
         else {
           const dat =  row.slice(0, pagelimit) 
           setselected(0)
           setdateshow(dat)
         }
    }

    useEffect(() => {
      const getrejecteddata =  async () => {
        const list = await getrejectedraison()
          setrowsPerPage(list) 
          const row = list
            const dat =  row.slice(offset, pagelimit+offset)
            if(Boolean(dat.length))
           setdateshow(dat)
           else {
             const dat =  row.slice(0, pagelimit) 
             setselected(0)
             setdateshow(dat)
           }
      }

      getrejecteddata()
    }, [offset , pagelimit])

    const   onPageChanged = (data) => {
        let selected = data.selected;
      let offset = Math.ceil(selected * pagelimit);
      if(Boolean(filterval.length))
      { const currentdata = filterval.slice(offset, offset +pagelimit)
       setoffset(offset)
       setselected(selected)
      setdateshow(currentdata)
      }
      else{
        const currentdata = rowsPerPage.slice(offset, offset +pagelimit)
        setoffset(offset)
        setselected(selected)
       setdateshow(currentdata)
       }
      }
      const Updaterow = (el) => {
        setupdatedata(el)
        setdrowerupdate(true)
      }
    return (
                 <div className="container">
                              <h3>{t("rejected.maintitel")}</h3>
      <hr/>
         
                <div onClick={() => setdrowerupdate(false)} className={drowerupdate ?"overlay is-open":"overlay"}></div>
            <div className={(drowerupdate && lng==="ar")   ? "arnav-menu  active" :drowerupdate && lng==="en"   ?"nav-menu  active": "nav-menu"}>
       
      
       {drowerupdate &&  <FormUpdate  hideSideBar={setdrowerupdate} rowdata ={updatedata} refrechdata= {getrejecteddata}  /> }
      </div>
             <div dir="ltr " className= {!Boolean(dateshow.length) ? "d-none float-right mt-4" :"float-right mt-4"}>
             <ReactPaginate
          previousLabel={<i className="fal fa-chevron-left"></i>}
          nextLabel={<i className="fal fa-angle-right"></i>}
          breakLabel={'...'}
          breakClassName={'break-me'}
          pageCount={filterval.length >0 ?Math.ceil(filterval.length / pagelimit) : Math.ceil(rowsPerPage.length / pagelimit)}
          marginPagesDisplayed={2}
          pageRangeDisplayed={2}
          onPageChange={onPageChanged}
          containerClassName={'pagination'}
          activeClassName={'active'}
          initialPage={0}
          forcePage={selected}
        /> </div>
           <div className="table-responsive mt-2">
             <table className="table table-borderless ">
          <thead>
            <tr className=" th-table">
              <th className="col ">{t("rejected.Id")}</th>
              <th className="col">{t("rejected.status")}</th>
              <th className="col">{t("rejected.titel")}</th>
              <th className="col">{t("rejected.Action")}</th>
            </tr>
          </thead>

          <tbody>
            {" "}
           
            {
              dateshow.map((el, index) => {
                return (
                  <tr key={index} className="row custom-tr ">
                    <td label={t("rejected.Id")} className="col">{el._id.slice(el._id.length - 3,el._id.length)}</td>
                    <td label={t("rejected.status")} className="col">{el.isEnabled ? "enabled" : "Not enabled"}</td>   
                    <td label={t("rejected.image")} className="col">{el.title[lng]}</td>
                    <td
                      label={t("rejected.Action")}
                      className="col"

      
        >
          <i className="fas fa-pen bagroudicon" onClick={()=>Updaterow(el)}></i> </td>
                  </tr>
                );
              })}{" "}
          </tbody>
        </table>
        </div>

  

        </div>
    )
}
