import React, {useContext, useState, useEffect} from 'react';

import { LayoutContext } from '../../contexts/LayoutContext';
import Slider from './slider/Slider';
import MobileMenu from '../../layout/mobileMenu/MobileMenu';
import {getgrouped} from '../../services/categoriesService';


import Categories from './categories/Categories';
import topSalesLogo from '../../assets/TopsaleLogo.png';
import appstore from '../../assets/appstore.png';
import playstore from '../../assets/playstore.png';


import './styles/Header.scss';

const Header = () => {
  const [showMobileMenu, setShowMobileMenu] = useState(false);
  const layoutContext = useContext(LayoutContext);
  const {setShowFilters, setSubCategory, setAdsQuery } = layoutContext;
  const [groupedSubCategories, setGroupedSubCategories] = useState([]);

  const APPSTORELINK =
    'https://apps.apple.com/ie/app/top-sale-qatar-%D8%AA%D9%88%D8%A8-%D8%B3%D9%8A%D9%84/id1161177948';
  const PLAYSTORELINK =
    'https://play.google.com/store/apps/details?id=qa.topsale';

      useEffect(() => {
        const loadCategories = async () => {
          const subcategoriesList = await getgrouped();
          setGroupedSubCategories(subcategoriesList.data.data);
        };

        loadCategories();
      }, []);


  const getAppPage = Link => {
    const win = Link !== '/' 
      ? window.open(Link, '_blank') 
      : window.location.replace(Link, '_blank');
    win.focus();
  };

  const handleSearch = e => {
    setAdsQuery(query =>  ({...query, keyword: e.target.value }));
  }

  const showHideMobileMenu = () => {
    setShowMobileMenu(show => !show);
  }

  return (
    <div className="header">
      <div className="header-container">
        <div className="logo-search">
          <img
            className="logo"
            src={topSalesLogo}
            alt="Top Sales"
            onClick={() => {
              setShowFilters(false);
              setSubCategory(null);
              getAppPage('/');
            }}
          />
          <div className="search">
            <input type="text" placeholder="search" onChange={handleSearch} />
            <div className="icon-container">
              <i className="fa fa-search" />
            </div>
          </div>
          {showMobileMenu && (
            <>
              <div className="global-overlay" />
              <MobileMenu
                setShowMobileMenu={setShowMobileMenu}
                setAdsQuery={setAdsQuery}
                showHideMobileMenu={showHideMobileMenu}
                groupedSubCategories={groupedSubCategories}
                showMobileMenu={showMobileMenu}
                setShowFilters={setShowFilters}
              />
            </>
          )}
          <button onClick={showHideMobileMenu} className="mobile-menu">
            <i className="menu-icon fa fa-bars" />
          </button>
        </div>
        <div className="download-app">
          <div className="apps-text">
            <div className="first-text">The simpler way to buy and sell</div>
            <div className="second-text">Get the app !</div>
          </div>
          <div class="group-btn-download">
            <button
              class="btn-appstore"
              onClick={() => getAppPage(APPSTORELINK)}
            >
              <img src={appstore} alt="" className="img-appstore" />
              <div class="content-button">
                <p class="desc-appstore">Download on the</p>
                <h4 class="title-appstore">App Store</h4>
              </div>
            </button>
            <button
              class="btn-appstore"
              onClick={() => getAppPage(PLAYSTORELINK)}
            >
              <img src={playstore} alt="" className="img-playstore" />
              <div class="content-button">
                <p class="desc-playstore">Android app on</p>
                <h4 class="title-appstore">Google play</h4>
              </div>
            </button>
          </div>
        </div>
        <div className="right-header">
          <button>
            New Ad
            <i class="fa fa-camera" />
          </button>
          <button className="language">
            language
            <i class="fa fa-globe" />
          </button>
          <button className="login">log in</button>
        </div>
      </div>
      <Slider
        setAdsQuery={setAdsQuery}
        setShowFilters={setShowFilters}
        setSubCategory={setSubCategory}
        style={{display: 'flex', width: '50%'}}
      />
      <div className="categories">
        <Categories />
      </div>
    </div>
  );
}

export default Header;
